<template>
	
<div class="city_page">
	


	<header-top title="选择地区"></header-top>
	<div class="fixed-top-div"></div>

<div class='search_group'>
	<!-- <label for="">名称</label> -->
	<input type="text" v-model:value="queryWords" placeholder="请输入查找地区名称">
</div>

<div class="city_list">
	
	<h4 class='title'>{{ queryWords != ''?'搜索结果':'所有地区'}}</h4>

	<ul class='hot clear' @click='selectCity'>
		<!-- <li v-for='(item,index) in filterList' :data-superId="item.a_super" :data-id="item.a_id" :data-level="item.a_level">{{item.a_name}}</li> -->


		

		<li v-for='(item,index) in filterList' 
			:data-superId="item.a_super" 
			:data-topId=" item.a_level==3?'QY201606018110':'' " 
			:data-id="item.a_id" 
			:data-level="item.a_level">{{item.a_name}}</li>



	</ul>



</div>


</div>


</template>

<script>
	
import city from '@/data/city'
import headerTop from '@/components/header/header'

// console.log(city);

export default{

	components:{
		headerTop
	},
	created(){
		// console.log(city);
	},
	mounted(){
		this.$nextTick(()=>{
			//console.log(this.filterList);
		})
	},
	methods:{
		selectCity(){
			let e = event || window.event;
			let target = event.target || e.scrElement;

			// console.log(target);
			this.$emit('selectCity',target)
			// return target;
		}
	},
	computed:{
		filterList(){
			return city.filter((val)=>{

				if(val.a_name.indexOf(this.queryWords)>=0){

					return val;

				}

				// console.log(val);
				//return val
			})
		}
	},
	data(){
		return{
			list:city,
			queryWords:""
		}
	}


}


</script>

<style lang='less' scoped>

@import '../../assets/css/variable';
.city_page{
	.fixed();
	z-index:999;
}



.search_group{

    width: 90%;
    margin:.5rem auto;
    text-align: center;


	input{

	    border: 1px solid #e4e4e4;
	    padding: .3rem 0.3rem;
	    font-size: 0.6rem;
	    color: #333;
	    width:90%;
	    border-radius:.5rem;


	}

}

.city_list{


	.title{
	    color: #666;
	    font-weight: 400;
	    text-indent: 0.45rem;
	    border-top: 1px solid #e4e4e4;
	    border-bottom: 1px solid #e4e4e4;
	    font: 0.55rem/1.45rem "Helvetica Neue";
	}

	.hot{


		li{

			float: left;
		    text-align: center;
		    color: #3190e8;
		    border-bottom: 0.025rem solid #e4e4e4;
		    border-right: 0.025rem solid #e4e4e4;
		    width: 25%;
		    height: 1.75rem;
		    font: 0.6rem/1.75rem "Microsoft YaHei";
		    overflow: hidden;

		}
	}
}



</style>


